@import url(https://fonts.googleapis.com/css2?family=Nunito+Sans:wght@300;600;800&display=swap);
.navbar{background-color:var(--BackgroundElmns);box-shadow:0px 6px 9px 0px var(--Boxshadow)}.navbar-brand{font-weight:bold;font-size:1.5rem;color:var(--textColor) !important}.navbar .darkMode{margin-left:auto;color:var(--textColor);text-transform:capitalize;font-size:1rem;font-weight:600;cursor:pointer}.navbar .darkMode svg{margin-right:5px;-webkit-transform:translateY(-1px);transform:translateY(-1px)}
.card{padding-bottom:2rem;cursor:pointer;border-radius:9px;overflow:hidden;background-color:var(--BackgroundElmns) !important;color:var(--textColor)}.card .card-img-top{height:13rem;border-radius:unset}.card-body{padding-left:18px}.card-title{font-weight:700;padding:1rem 0}.card-text{margin-bottom:5px}
.homepage{margin-top:3rem}.homepage .searchBox{padding:.5rem;background-color:var(--BackgroundElmns);box-shadow:0px 0px 6px 2px var(--Boxshadow);border-radius:6px}.homepage .searchBox input,.homepage .searchBox button{box-shadow:unset !important;background-color:rgba(0,0,0,0) !important;border:none}.homepage .searchBox input::-webkit-input-placeholder{font-size:.9rem;font-weight:600;color:var(--InputColor)}.homepage .searchBox input:-ms-input-placeholder{font-size:.9rem;font-weight:600;color:var(--InputColor)}.homepage .searchBox input,.homepage .searchBox input::placeholder{font-size:.9rem;font-weight:600;color:var(--InputColor)}.homepage .searchBox button.btn-primary:not(:disabled):not(.disabled).active,.homepage .searchBox button.btn-primary:not(:disabled):not(.disabled):active,.homepage .searchBox button.show>.btn-primary.dropdown-toggle{border-color:rgba(0,0,0,0)}.homepage .searchBox button svg{color:var(--InputColor)}.infinite-scroll-component{overflow:hidden !important;overflow-y:auto}.loadingMsg{height:35vh;align-items:center;color:var(--textColor)}
.countryPage{margin-top:4rem}.countryPage img{width:90%;height:23rem;object-fit:cover}.countryPage .btn{background-color:var(--BackgroundElmns);box-shadow:0px 0px 6px 2px var(--Boxshadow);color:var(--textColor);padding:.5rem 2.3rem;margin-bottom:4rem}.countryPage .btn svg{margin-right:.4rem}.countryPage_info{color:var(--textColor)}.countryPage_info h3{margin-bottom:2rem}.countryPage_info h5{font-weight:600;font-size:1rem;text-transform:capitalize;margin-bottom:1rem}.countryPage_info h5 span{font-weight:400;padding-left:5px}.countryPage_info .borders{margin-top:3rem}.countryPage_info .borders h5{white-space:nowrap;margin-right:1rem}.countryPage_info .borders .badge{color:var(--textColor);font-size:.8rem;margin-right:10px;padding:8px 25px;margin-bottom:.7rem;cursor:pointer;background-color:var(--BackgroundElmns) !important}
*{font-family:"Nunito Sans",sans-serif}:root{--Background: #fafafa;--BackgroundElmns: #fff;--textColor: #111517;--InputColor: #858585;--Boxshadow: #E3E3E3}[data-theme=dark]{--Background: #202c37;--BackgroundElmns: #2b3945;--textColor: #fff;--InputColor: #CDD3D6;--Boxshadow: #202D36}.app{min-height:100vh;background-color:#fafafa;background-color:var(--Background)}.container-fluid{padding-right:4rem !important;padding-left:4rem !important}.main_shadow{box-shadow:0px 6px 9px 0px #E3E3E3;box-shadow:0px 6px 9px 0px var(--Boxshadow)}

