@import '../../index.scss';
.homepage {
    margin-top: 3rem;
    .searchBox {
        padding: 0.5rem;
        background-color: var(--BackgroundElmns);
        box-shadow: 0px 0px 6px 2px var(--Boxshadow);
        border-radius: 6px;
        input,
        button {
            box-shadow: unset !important;
            background-color: transparent !important;
            border: none;
        }
        input {
            &,
            &::placeholder {
                font-size: .9rem;
                font-weight: 600;
                color: var(--InputColor);
            }
        }
        button {
            &.btn-primary:not(:disabled):not(.disabled).active,
            &.btn-primary:not(:disabled):not(.disabled):active,
            &.show>.btn-primary.dropdown-toggle {
                border-color: transparent;
            }
            svg {
                color: var(--InputColor);
            }
        }
    }
}

.infinite-scroll-component {
    overflow: hidden!important;
    overflow-y: auto;
}

.loadingMsg {
    height: 35vh;
    align-items: center;
    color: var(--textColor);
}